import { useState, useRef, useEffect } from 'react';
import {
  Container,
  Typography,
  Box,
  Grid,
  Card,
  CardContent,
  CardMedia,
  Chip,
  Button,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  IconButton,
  useMediaQuery,
  useTheme,
} from '@mui/material';
import { motion } from 'framer-motion';
import CloseIcon from '@mui/icons-material/Close';
import ZoomInIcon from '@mui/icons-material/ZoomIn';

//  import
import project1Thumb from '../img/projectImg/1_khoa.png';
import project1Detail1 from '../img/projectImg/1_khoa.png';
import project1Detail2 from '../img/projectImg/1_khoa2.png';
import project1Detail3 from '../img/projectImg/1_khoa3.png';

import project2Thumb from '../img/projectImg/2_kofdi.png';
import project2Detail1 from '../img/projectImg/2_kofdi.png';
import project2Detail2 from '../img/projectImg/2_kofdi2.png';
import project2Detail3 from '../img/projectImg/2_kofdi3.png';

import project3Thumb from '../img/projectImg/3_oceanmap.png';
import project3Detail1 from '../img/projectImg/3_oceanmap.png';

import project4Thumb from '../img/projectImg/4_marine.png';
import project4Detail1 from '../img/projectImg/4_marine.png';
import project4Detail2 from '../img/projectImg/4_marine2.png';
import project4Detail3 from '../img/projectImg/4_marine3.png';

import project5Thumb from '../img/projectImg/5_oceanmap.png';
import project5Detail1 from '../img/projectImg/5_oceanmap.png';
import project5Detail2 from '../img/projectImg/5_oceanmap2.png';

import project6Thumb from '../img/projectImg/6_tp.png';
import project6Detail1 from '../img/projectImg/6_tp.png';
import project6Detail2 from '../img/projectImg/6_tp2.png';
import project6Detail3 from '../img/projectImg/6_tp3.png';

import project7Thumb from '../img/projectImg/7.ioc.png.jpg';
import project7Detail1 from '../img/projectImg/7.ioc.png.jpg';
import project7Detail2 from '../img/projectImg/7.ioc2.png.jpg';

interface Project {
  id: number;
  title: string;
  description: string;
  image: string;
  technologies: string[];
  demo: string;
  detailImages: string[];
  myPart: string;
}

const projects: Project[] = [
  {
    id: 1,
    title: '  ',
    description: '    ()     ',
    image: project1Thumb,
    technologies: ['Java', 'JavaScript', 'PostgreSQL', 'Oracle'],
    demo: 'https://example.com/portfolio',
    detailImages: [project1Detail1, project1Detail2, project1Detail3],
    myPart: '   JavaScript , ()          .      Burp Suite        ,        .         ,          .     ,       .           .'
  },
  {
    id: 2,
    title: '  ',
    description: '     ()  NC        ',
    image: project2Thumb,
    technologies: ['Java', 'JavaScript', 'PostgreSQL', 'Oracle', 'GeoServer', 'OpenLayers'],
    demo: 'https://example.com/shopping',
    detailImages: [project2Detail1, project2Detail2, project2Detail3],
    myPart: '       ,      HWP    . HTML     ,              . , NC       QGIS   ,  JSON    Mohid      .   NC  QGIS      ,          .          ,           .'
  },
  {
    id: 3,
    title: '    ',
    description: '        ',
    image: project3Thumb,
    technologies: ['Linux(redhat)', 'PostgreSQL', 'Oracle'],
    demo: 'https://example.com/social',
    detailImages: [project3Detail1],
    myPart: '      5    .   Linux         .         ,         .    Linux   ,    2025        . Linux       ,              .'
  },
  {
    id: 4,
    title: '  ',
    description: '    ()     ',
    image: project4Thumb,
    technologies: ['Java', 'JavaScript', 'PostgreSQL', 'Oracle', 'OpenLayers'],
    demo: 'https://example.com/marine',
    detailImages: [project4Detail1, project4Detail2, project4Detail3],
    myPart: '    ,     .    , ,        .      ,     DB   .      ,      DB        .   ,         ,       .         .      ,       ,              .'
  },
  {
    id: 5,
    title: '  ',
    description: '   ',
    image: project5Thumb,
    technologies: ['Java', 'JavaScript', 'PostgreSQL', 'Oracle'],
    demo: 'https://example.com/autonomous',
    detailImages: [project5Detail1, project5Detail2],
    myPart: '2024 7            .    ,                .     ,               .         ,     .'
  },
  {
    id: 6,
    title: ' API  ',
    description: ' API  / ',
    image: project6Thumb,
    technologies: ['Java', 'Spring Boot', 'React','PostgreSQL'],
    demo: 'https://example.com/weather',
    detailImages: [project6Detail1, project6Detail2, project6Detail3],
    myPart: '         API       .  DB     ,     2   .         ,       ,             .       ,         ,          .          ,       .                .'
  },
  {
    id: 7,
    title: '   (   )',
    description: '    ',
    image: project7Thumb,
    technologies: ['marine knowledge'],
    demo: 'https://example.com/mobile',
    detailImages: [project7Detail1, project7Detail2],
    myPart: '                 . ISO(  )   S-100, S-131      5  ICMASS 2025                     .'
  },
];

const Projects = () => {
  const [selectedProject, setSelectedProject] = useState<Project | null>(null);
  const [currentIndex, setCurrentIndex] = useState(0);
  const [selectedImage, setSelectedImage] = useState<string | null>(null);
  const scrollContainerRef = useRef<HTMLDivElement>(null);
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down('sm'));

  useEffect(() => {
    const interval = setInterval(() => {
      if (scrollContainerRef.current) {
        const container = scrollContainerRef.current;
        const scrollAmount = container.offsetWidth * 0.8;
        const maxScroll = container.scrollWidth - container.offsetWidth;
        
        if (container.scrollLeft >= maxScroll) {
          container.scrollTo({ left: 0, behavior: 'smooth' });
        } else {
          container.scrollTo({
            left: container.scrollLeft + scrollAmount,
            behavior: 'smooth'
          });
        }
      }
    }, 5000);

    return () => clearInterval(interval);
  }, []);

  const handleOpenDialog = (project: Project) => {
    setSelectedProject(project);
  };

  const handleCloseDialog = () => {
    setSelectedProject(null);
  };

  const handleDemoClick = (url: string) => {
    window.open(url, '_blank', 'width=800,height=600');
  };

  const handleImageClick = (image: string) => {
    setSelectedImage(image);
  };

  const handleCloseImage = () => {
    setSelectedImage(null);
  };

  return (
    <Box sx={{ 
      minHeight: '100vh',
      background: 'linear-gradient(180deg, rgba(15, 23, 42, 0.8) 0%, rgba(15, 23, 42, 0.95) 100%)',
      py: 8,
      overflow: 'hidden'
    }}>
      <Container maxWidth="xl" sx={{ px: { xs: 1, sm: 2 } }}>
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.8 }}
        >
          <Typography
            variant="h2"
            component="h1"
            gutterBottom
            align="center"
            sx={{
              mb: 2,
              color: 'primary.main',
              fontSize: { xs: '2.5rem', md: '3.5rem' },
              fontWeight: 700,
            }}
          >
            
          </Typography>
          <Typography
            variant="h5"
            color="text.secondary"
            paragraph
            align="center"
            sx={{ 
              mb: 8, 
              maxWidth: '800px', 
              mx: 'auto',
              fontSize: { xs: '1.1rem', md: '1.25rem' },
              lineHeight: 1.6,
            }}
          >
                
          </Typography>
        </motion.div>

        <Grid container spacing={4}>
          {projects.map((project, index) => (
            <Grid item xs={12} sm={6} md={6} key={project.id}>
              <motion.div
                initial={{ opacity: 0, y: 20 }}
                animate={{ opacity: 1, y: 0 }}
                transition={{ duration: 0.5, delay: index * 0.1 }}
              >
                <Card
                  sx={{
                    height: '100%',
                    display: 'flex',
                    flexDirection: 'column',
                    bgcolor: 'rgba(255, 255, 255, 0.05)',
                    backdropFilter: 'blur(10px)',
                    borderRadius: '24px',
                    border: '1px solid rgba(255, 255, 255, 0.1)',
                    transition: 'all 0.3s ease-in-out',
                    '&:hover': {
                      transform: 'translateY(-8px)',
                      boxShadow: '0 20px 40px rgba(0, 0, 0, 0.3)',
                      bgcolor: 'rgba(255, 255, 255, 0.08)',
                    },
                    minHeight: '650px',
                  }}
                >
                  <CardMedia
                    component="img"
                    image={project.image}
                    alt={project.title}
                    sx={{ 
                      objectFit: 'cover',
                      borderTopLeftRadius: '24px',
                      borderTopRightRadius: '24px',
                      imageRendering: 'optimizeQuality',
                      transform: 'scale(1)',
                      transition: 'transform 0.3s ease-in-out',
                      '&:hover': {
                        transform: 'scale(1.02)',
                      },
                      width: '100%',
                      aspectRatio: '16/9',
                    }}
                  />
                  <CardContent sx={{ 
                    flexGrow: 1, 
                    p: 4,
                    display: 'flex',
                    flexDirection: 'column',
                    justifyContent: 'space-between',
                  }}>
                    <Box>
                      <Typography 
                        variant="h5" 
                        component="h2" 
                        gutterBottom
                        sx={{ 
                          fontWeight: 600,
                          mb: 3,
                          color: 'primary.main',
                          fontSize: '1.5rem',
                        }}
                      >
                        {project.title}
                      </Typography>
                      <Typography 
                        color="text.secondary" 
                        paragraph
                        sx={{ 
                          mb: 4,
                          lineHeight: 1.8,
                          fontSize: '1.2rem',
                        }}
                      >
                        {project.description}
                      </Typography>
                      <Box sx={{ mb: 4 }}>
                        {project.technologies.map((tech) => (
                          <Chip
                            key={tech}
                            label={tech}
                            size="medium"
                            sx={{
                              mr: 1,
                              mb: 1,
                              bgcolor: 'rgba(99, 102, 241, 0.1)',
                              color: 'primary.main',
                              fontWeight: 500,
                              fontSize: '1rem',
                              height: '36px',
                              '&:hover': {
                                bgcolor: 'rgba(99, 102, 241, 0.2)',
                              },
                            }}
                          />
                        ))}
                      </Box>
                    </Box>
                    <Box sx={{ display: 'flex', gap: 2 }}>
                      <Button
                        variant="contained"
                        onClick={() => handleOpenDialog(project)}
                        sx={{ 
                          width: '100%',
                          py: 2.5,
                          borderRadius: '12px',
                          textTransform: 'none',
                          fontSize: '1.2rem',
                          fontWeight: 600,
                          bgcolor: 'primary.main',
                          '&:hover': {
                            bgcolor: 'primary.dark',
                          },
                        }}
                      >
                         
                      </Button>
                    </Box>
                  </CardContent>
                </Card>
              </motion.div>
            </Grid>
          ))}
        </Grid>
      </Container>

      <Dialog
        open={!!selectedProject}
        onClose={handleCloseDialog}
        maxWidth="md"
        fullWidth
        PaperProps={{
          sx: {
            bgcolor: 'background.paper',
            borderRadius: '24px',
            border: '1px solid rgba(255, 255, 255, 0.1)',
          }
        }}
      >
        {selectedProject && (
          <>
            <DialogTitle sx={{ 
              pb: 2,
              color: 'primary.main',
              fontWeight: 600,
            }}>
              {selectedProject.title}
              <IconButton
                onClick={handleCloseDialog}
                sx={{ 
                  position: 'absolute', 
                  right: 16, 
                  top: 16,
                  color: 'text.secondary',
                  '&:hover': {
                    color: 'primary.main',
                  },
                }}
              >
                <CloseIcon />
              </IconButton>
            </DialogTitle>
            <DialogContent>
              <Grid container spacing={3} sx={{ mt: 1 }}>
                {selectedProject.detailImages.map((image, index) => (
                  <Grid item xs={12} md={4} key={index}>
                    <Box
                      sx={{
                        position: 'relative',
                        cursor: 'pointer',
                        '&:hover .zoom-icon': {
                          opacity: 1,
                        },
                      }}
                      onClick={() => handleImageClick(image)}
                    >
                      <Box
                        component="img"
                        src={image}
                        alt={`${selectedProject.title}   ${index + 1}`}
                        sx={{
                          width: '100%',
                          height: '200px',
                          borderRadius: '16px',
                          objectFit: 'cover',
                          transition: 'transform 0.3s ease-in-out',
                          '&:hover': {
                            transform: 'scale(1.05)',
                          },
                        }}
                      />
                      <Box
                        className="zoom-icon"
                        sx={{
                          position: 'absolute',
                          top: '50%',
                          left: '50%',
                          transform: 'translate(-50%, -50%)',
                          bgcolor: 'rgba(0, 0, 0, 0.5)',
                          borderRadius: '50%',
                          p: 1,
                          opacity: 0,
                          transition: 'opacity 0.3s ease-in-out',
                        }}
                      >
                        <ZoomInIcon sx={{ color: 'white', fontSize: 32 }} />
                      </Box>
                    </Box>
                  </Grid>
                ))}
              </Grid>
              <Box sx={{ mt: 4 }}>
                <Typography 
                  variant="h6" 
                  gutterBottom
                  sx={{ 
                    fontWeight: 600,
                    color: 'primary.main',
                  }}
                >
                   
                </Typography>
                <Typography 
                  color="text.secondary"
                  sx={{ 
                    lineHeight: 1.8,
                    fontSize: '1.1rem',
                  }}
                >
                  {selectedProject.myPart}
                </Typography>
              </Box>
            </DialogContent>
            <DialogActions sx={{ px: 3, pb: 3 }}>
              <Button 
                onClick={handleCloseDialog}
                variant="contained"
                sx={{ 
                  borderRadius: '12px',
                  textTransform: 'none',
                  px: 4,
                  py: 1,
                }}
              >
                
              </Button>
            </DialogActions>
          </>
        )}
      </Dialog>

      <Dialog
        open={!!selectedImage}
        onClose={handleCloseImage}
        maxWidth="lg"
        fullWidth
        PaperProps={{
          sx: {
            bgcolor: 'transparent',
            boxShadow: 'none',
            overflow: 'hidden',
          }
        }}
      >
        {selectedImage && (
          <>
            <IconButton
              onClick={handleCloseImage}
              sx={{ 
                position: 'absolute', 
                right: 16, 
                top: 16,
                color: 'white',
                bgcolor: 'rgba(0, 0, 0, 0.5)',
                '&:hover': {
                  bgcolor: 'rgba(0, 0, 0, 0.7)',
                },
              }}
            >
              <CloseIcon />
            </IconButton>
            <Box
              component="img"
              src={selectedImage}
              alt=" "
              sx={{
                width: '100%',
                height: 'auto',
                maxHeight: '90vh',
                objectFit: 'contain',
              }}
            />
          </>
        )}
      </Dialog>
    </Box>
  );
};

export default Projects; 